body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: 'Oswald', sans-serif;
  background-color: #f1d3f7;
}
p {
  margin: 0;
  padding: 0;
}
.container {
  min-width: 280px;
  margin: 0 auto;
}
.button {
  display: block;
  padding: 25px 40px;
  width: 60%;
  text-align: center;
  text-transform: uppercase;
  color: #000;
  text-decoration: none;
  border-radius: 40px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}
.button:hover {
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.35);
  color: #fff;
  background-color: #c33181;
}
@media (min-width: 767px) {
  body {
    background-color: #f7d1fa;
  }
  .container {
    width: 640px;
    padding: 0 60px;
  }
}
@media (min-width: 1108px) {
  .container {
    width: 960px;
    padding: 0 80px;
  }
}

.logo {
  display: flex;
  justify-content: center;
  padding-top: 12px;
  min-height: 200px;
  position: relative;
  background-image: url('../../img/bg-desktop.png');
  background-size: cover;
  background-repeat: no-repeat;
}
.page-header {
  margin-bottom: 40px;
  position: relative;
}
.logo__img {
  background-image: url('../../img/logo-super.png');
  position: absolute;
  top: 23px;
  left: 20px;
  background-size: 240px;
  width: 240px;
  height: 150px;
  background-repeat: no-repeat;
  animation-name: logo;
  animation-duration: 4s;
  animation-delay: 0.1s;
  animation-iteration-count: infinite;
}
@keyframes logo {
  0% {
    transform: translateY(0px);
  }
  60% {
    transform: translateY(-20px);
  }
  100% {
    transform: translateY(0px);
  }
}
.logo__title {
  font-weight: 400;
  margin: 0;
  padding: 0;
  font-size: 38px;
  color: #fff;
  text-shadow: 0 0px 20px black;
  margin-top: 0px;
}
.logo__button {
  margin: 0;
  padding: 0;
  margin-left: auto;
  margin-right: 30px;
  border: none;
  font: inherit;
  width: 60px;
  height: 60px;
  margin-top: 20px;
  background-color: #ebc1f6;
  cursor: pointer;
  position: relative;
  border-radius: 10px;
  top: -5px;
  right: -18px;
}
.logo__button--burger {
  width: 30px;
  height: 3px;
  background-color: #fff;
  display: block;
  margin-left: 10px;
  position: relative;
  left: 5px;
}
.logo__button--burger::after,
.logo__button--burger::before {
  content: "";
  width: 30px;
  height: 3px;
  background-color: #fff;
  position: absolute;
  top: 2px;
  left: 0;
}
.logo__button--burger::after {
  top: 2px;
  transform: translateY(8px);
}
.logo__button--burger::before {
  top: 2px;
  transform: translateY(-12px);
}
.logo__button--bureger--active .logo__button--burger::after {
  top: 0px;
  transform: rotate(-45deg);
}
.logo__button--bureger--active .logo__button--burger::before {
  top: 0px;
  transform: rotate(45deg);
}
.logo__button--bureger--active .logo__button--burger {
  width: 0;
}
.logo__block {
  margin-left: 50px;
}
.main-nav {
  display: block;
  background-color: #f8f5f2;
  position: relative;
  z-index: 10;
  margin-top: -80px;
}
.site-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.site-nav__item {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  padding: 15px 20px;
}
.site-nav__item:hover {
  background-color: #dadada;
}
.site-nav__item:active {
  background-color: #dadada;
}
.site-nav__item--active {
  background-color: #bfbfbe;
}
.site-nav__item a {
  font-size: 18px;
  line-height: 24px;
  color: black;
  width: 200px;
  text-decoration: none;
  text-transform: uppercase;
}
.site-nav__item:hover a,
.site-nav__item:active a {
  color: #fff;
}
.button--closed {
  display: none;
}
.button--opened {
  display: block;
}
.main-nav__info {
  display: none;
}
@media (min-width: 767px) {
  .page-header {
    min-height: 500px;
    background-image: url('../../img/bg-desktop.png');
    background-size: cover;
  }
  .main-nav {
    display: block;
    background-color: #f8f5f2;
    position: relative;
    z-index: 10;
    margin-top: 0px;
  }
  .container {
    display: flex;
    flex-direction: column;
  }
  .logo {
    display: flex;
    justify-content: center;
    padding-top: 12px;
    min-height: 300px;
    position: relative;
    background-image: url('../../img/orhideya.png');
    background-size: 300px;
    background-repeat: no-repeat;
  }
  .logo__button {
    display: none;
  }
  .logo__img {
    background-image: url('../../img/logo-super.png');
    top: 170px;
    left: 280px;
    background-size: 420px;
    width: 420px;
    height: 170px;
  }
  .button--closed {
    display: block;
  }
  .site-nav__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    justify-content: flex-end;
  }
  .main-nav {
    order: -1;
    width: auto;
    background-color: transparent;
  }
  .site-nav__item {
    border-bottom: none;
    padding: 15px 20px;
  }
  .site-nav__item:hover {
    background-color: #f7d1fa;
  }
  .site-nav__item:active {
    background-color: #f7d1fa;
  }
  .main-nav__info {
    margin-top: 10px;
    display: flex;
    align-items: flex-end;
    flex-direction: column;
  }
}
@media (min-width: 1108px) {
  .logo__img {
    background-image: url('../../img/logo-super.png');
    top: 270px;
    left: -70px;
  }
  .logo {
    margin-left: 355px;
    margin-top: -50px;
  }
}

.info {
  min-height: 180px;
  margin-bottom: 60px;
  margin-top: -130px;
  z-index: 5;
  position: relative;
}
.info__title,
.info__text {
  margin: 0;
  padding: 0;
  text-align: center;
}
.info__block1 {
  background-color: #f6eef7;
  border-radius: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  margin: 0 auto;
  max-width: 95%;
}
.info__title {
  padding: 30px 65px;
  font-size: 30px;
  text-transform: uppercase;
  font-weight: 500;
}
.info__text {
  padding: 0 60px 30px 60px;
  font-size: 18px;
  line-height: 22px;
}
.info-list {
  width: 100%;
  border-collapse: collapse;
}
.info__value {
  margin: 0;
  width: 50%;
  padding: 20px;
  font-size: 25px;
  font-weight: 500;
  text-align: center;
}
tr {
  background-color: #f8f5f2;
}
tr:nth-child(2n) {
  background-color: #f8efe6;
}
.info__field {
  width: 100%;
  text-align: center;
  font-size: 16px;
  font-weight: 400;
  padding: 20px;
}
.info__button {
  margin: 20px auto;
  background-color: #fff4c2;
}
@media (min-width: 767px) {
  .info__button {
    order: -1;
    width: 40%;
    padding: 30px 40px;
  }
  .info {
    margin-top: 0;
    min-height: 180px;
    margin-bottom: 60px;
  }
  .info__block1 {
    background-color: #f6eef7;
    border-radius: 0px;
    box-shadow: none;
    margin: 0 auto;
    width: auto;
  }
  .info-list {
    box-shadow: 0 3px 7px rgba(0, 0, 0, 0.5);
  }
  .info__button {
    margin: 40px auto;
  }
}

.features {
  width: 100%;
  height: 400px;
  margin-bottom: 70px;
}
.buttonleftslider {
  position: absolute;
  border: none;
  width: 30px;
  height: 145px;
  background: transparent;
  top: 120px;
}
.buttonleftslider::after,
.buttonleftslider::before {
  position: absolute;
  content: "";
  width: 3px;
  height: 40px;
  background-color: #a4a3a3;
  top: 33px;
}
.buttonleftslider::after {
  transform: rotate(20deg);
}
.buttonleftslider::before {
  transform: rotate(-20deg);
  top: 70px;
}
.buttonrightslider {
  position: absolute;
  border: none;
  width: 30px;
  height: 145px;
  background: transparent;
  top: 120px;
  right: 0;
}
.buttonrightslider::after,
.buttonrightslider::before {
  position: absolute;
  content: "";
  width: 3px;
  height: 40px;
  background-color: #a4a3a3;
  top: 33px;
}
.buttonrightslider::after {
  transform: rotate(-20deg);
}
.buttonrightslider::before {
  transform: rotate(20deg);
  top: 70px;
}
.features__wrapper {
  margin: 0 auto;
  width: 300px;
  height: 400px;
  position: relative;
  overflow: hidden;
  border-radius: 10px;
}
.features__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  width: 900px;
  position: relative;
  transition: all ease 0.7s;
  left: 0;
}
.features__item {
  width: 300px;
  height: 400px;
  border-radius: 10px;
  background-color: #f6f4f1;
}
.features__img {
  width: 90px;
  height: 90px;
  background-color: red;
  margin: 0 auto;
  margin-top: 85px;
}
.features__title {
  margin: 30px auto 15px;
  width: 100px;
  text-align: center;
  text-transform: uppercase;
  font-size: 30px;
}
.features__text {
  margin: 30px auto 15px;
  width: 200px;
  text-align: center;
  text-transform: uppercase;
  font-size: 15px;
}
.features__img--img1 {
  background: url('../../img/slider1.png');
  background-size: cover;
  background-repeat: no-repeat;
}
.features__img--img2 {
  background: url('../../img/slider2.png');
  background-size: contain;
  background-repeat: no-repeat;
}
.features__img--img3 {
  background: url('../../img/slider3.png');
  background-size: contain;
  background-repeat: no-repeat;
}
@media (min-width: 767px) {
  .buttonleftslider,
  .buttonrightslider {
    display: none;
  }
  .features__wrapper {
    width: 800px;
    margin: 0 auto;
    overflow: visible;
  }
  .features__list {
    width: 820px;
  }
  .features__item {
    margin-right: 20px;
  }
  .features {
    margin-bottom: 130px;
  }
}


.news {
  min-height: 360px;
  padding: 0 20px;
  background-color: #f8f5f2;
  border-radius: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  max-width: 95%;
  margin-bottom: 50px;
  box-sizing: border-box;
}
.news__title {
  text-align: center;
  text-transform: uppercase;
  font-size: 30px;
  margin-bottom: 40px;
  margin-top: 40px;
}
.news__wrapper {
  display: flex;
  margin-bottom: 50px;
}
.news__date {
  width: 50px;
  height: 60px;
  border: 1px solid black;
  text-align: center;
  font-size: 30px;
  line-height: 30px;
  font-weight: bold;
  color: #fff;
  background-color: black;
  margin-top: 3px;
  border-radius: 10px;
  box-shadow: 3px 5px 10px rgba(0, 0, 0, 0.5);
  padding: 10px;
}
.news__text {
  padding: 0 0 0 30px;
}
@media (min-width: 767px) {
  .news {
    width: 700px;
    padding: 0 10px;
    flex-direction: row;
    margin-top: -50px;
  }
}

.page-footer {
  color: #fff;
  background-color: #292929;
  text-align: center;
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
}
.cart {
  margin: 0 auto;
  padding: 0 15px;
}
.main-nav__nomer a {
  font-size: 20px;
  color: black;
}
.main-nav__nomer a:hover {
  font-size: 20px;
  color: #cb24a1;
}
.page-footer__title {
  margin: 0;
  padding: 0;
  margin-top: 30px;
  text-transform: uppercase;
  text-decoration: underline;
  font-size: 22px;
  margin-bottom: 15px;
}
.page-footer__address {
  margin: 0;
  padding: 0;
  margin-bottom: 25px;
}
.page-footer__nomber a {
  border: 1px solid #414141;
  margin: 0 auto;
  padding: 15px 40px;
  display: block;
  margin-bottom: 20px;
  color: #fff;
  text-decoration: none;
  width: 50%;
}
.page-footer__nomber a:hover {
  background-color: white;
  color: black;
}
.page-footer__copyright {
  margin-bottom: 20px;
}
.page-footer__copyright--button {
  border: 1px solid #414141;
  margin: 0 auto;
  padding: 15px 40px;
  display: block;
  margin-bottom: 20px;
  color: #fff;
  width: 50%;
}
.page-footer__copyright--button:hover {
  background-color: white;
  color: black;
}
.tel {
  margin-top: 30px;
}
.page-footer__nomber--text {
  margin-bottom: 20px;
}
@media (min-width: 767px) {
  .page-footer {
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }
  .tel {
    margin-top: 35px;
  }
  .copyright {
    margin-top: 35px;
  }
  .page-footer__copyright--button {
    border: 1px solid #414141;
    width: 150px;
    margin: 0 auto;
    padding: 15px 40px;
    display: block;
    margin-bottom: 20px;
    color: #fff;
  }
  .page-footer__wrapper2 {
    display: flex;
    color: #fff;
    background-color: #292929;
    position: relative;
    min-width: 740px;
    justify-content: space-between;
    margin-top: -25px;
  }
  .page-footer__nomber a {
    border: 1px solid #414141;
    width: 150px;
    margin: 0 auto;
    padding: 15px 40px;
    display: block;
    margin-bottom: 20px;
    color: #fff;
    text-decoration: none;
  }
  .page-footer__nomber--text {
    margin-bottom: 20px;
  }
  .cart iframe {
    width: 640px;
    height: 400px;
  }
  .cart {
    width: 740px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 30px;
    border-bottom: 2px solid grey;
    flex-grow: 3;
  }
}
@media (min-width: 1108px) {
  .cart {
    width: 960px;
    min-width: 10px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 30px;
    border-bottom: 2px solid grey;
  }
  .page-footer__wrapper2 {
    width: 960px;
  }
  .cart iframe {
    width: 860px;
    height: 500px;
  }
}

.modal {
  width: 300px;
  height: 200px;
  position: fixed;
  border: 5px solid #f9df68;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 20px;
  background-color: #fff4c2;
  top: 150px;
  left: calc(50% - 150px);
  z-index: 15;
  text-align: center;
  animation-name: modal-open;
  animation-duration: 0.7s;
}
@keyframes modal-open {
  0% {
    transform: translateX(-1000px);
  }
  80% {
    transform: translateX(100px);
  }
  100% {
    transform: translateX(0px);
  }
}
.modal__title {
  font-weight: 400;
  width: 200px;
  margin: 30px auto;
}
.modal__phone {
  font-size: 28px;
  color: #646462;
}
.modal__phone:hover {
  color: #cb24a1;
}
.modal__close {
  position: absolute;
  border: 0;
  width: 40px;
  height: 40px;
  background-color: #cb24a1;
  right: -10px;
  top: -10px;
  cursor: pointer;
}
.modal__close::after {
  content: "";
  position: absolute;
  width: 30px;
  height: 4px;
  background-color: #fff;
  right: 5px;
  top: 18px;
  transform: rotate(45deg);
  border-radius: 5px;
}
.modal__close::before {
  content: "";
  position: absolute;
  width: 30px;
  height: 4px;
  background-color: #fff;
  right: 5px;
  top: 18px;
  transform: rotate(-45deg);
  border-radius: 5px;
}
.modal__close:hover::before,
.modal__close:hover::after {
  background-color: black;
}
.modal__exit {
  display: none;
}
.modal-cart {
  width: 320px;
  height: 350px;
  position: fixed;
  border: 5px solid #f9df68;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 20px;
  background-color: #fff4c2;
  top: 150px;
  left: calc(50% - 175px);
  z-index: 12;
  text-align: center;
  animation-name: modal-cart-open;
  animation-duration: 1s;
  padding-right: 15px;
}
@keyframes modal-cart-open {
  0% {
    transform: translateX(1000px);
  }
  100% {
    transform: translateX(0px);
  }
}
@media (min-width: 767px) {
  .modal {
    width: 500px;
    height: 300px;
    left: calc(50% - 250px);
  }
  .modal__title {
    font-weight: 500;
    width: 250px;
    margin: 45px auto;
  }
  .modal__phone {
    font-size: 35px;
    color: #646462;
  }
}
